html, body {
  height: 100vh;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  color: #fff;
  min-height: 100vh;
  font-family: sans-serif;
  background-color: #000;
}


.app-background {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 0;
  background: -webkit-gradient(linear, left bottom, left top, color-stop(50%, #011222),to(#1c2935));
  background: -webkit-linear-gradient(bottom, #011222 50%,#1c2935 100%);
  background: -o-linear-gradient(bottom, #011222 50%,#1c2935 100%);
  background: linear-gradient(to top, #011222 50%,#1c2935 100%);
}


