.welcome {
    height: 100vh;
    min-height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.welcome a {
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;
    border: 1px solid #fff;
    padding: 10px;
    font-weight: 700;
}

.welcome a:hover {
    border-color: #bdc3c7;
}


